'use client';

import { useState, useEffect } from 'react';
import { useRouter } from 'next/navigation';
import { Button } from '@/components/ui/button';
import { Input } from '@/components/ui/input';
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '@/components/ui/card';
import { Badge } from '@/components/ui/badge';
import { Tabs, TabsContent, TabsList, TabsTrigger } from '@/components/ui/tabs';
import { Avatar, AvatarFallback, AvatarImage } from '@/components/ui/avatar';
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from '@/components/ui/select';
import { useAuth } from '@/contexts/auth-context';
import { 
  Search, 
  Filter, 
  Star, 
  Clock, 
  DollarSign, 
  Calendar,
  BookOpen,
  Settings,
  LogOut,
  Heart,
  MessageCircle
} from 'lucide-react';

// Mock data for teachers
const mockTeachers = [
  {
    id: "1",
    name: "Sarah Johnson",
    bio: "Experienced English teacher with 8 years of teaching experience. Specialized in business English and conversation practice.",
    subjects: ["English", "Business English", "IELTS Preparation"],
    hourlyRate: 25,
    rating: 4.8,
    totalReviews: 127,
    languages: ["English", "Spanish"],
    timezone: "America/New_York",
    profileImage: "/placeholder-user.jpg",
    isOnline: true
  },
  {
    id: "2",
    name: "David Chen",
    bio: "Native Mandarin speaker with expertise in teaching Chinese to beginners and advanced students.",
    subjects: ["Mandarin", "Chinese Culture", "HSK Preparation"],
    hourlyRate: 30,
    rating: 4.9,
    totalReviews: 89,
    languages: ["Mandarin", "English"],
    timezone: "Asia/Shanghai",
    profileImage: "/placeholder-user.jpg",
    isOnline: false
  },
  {
    id: "3",
    name: "Maria Garcia",
    bio: "Professional Spanish tutor with focus on conversational Spanish and Latin American culture.",
    subjects: ["Spanish", "Latin Culture", "DELE Preparation"],
    hourlyRate: 22,
    rating: 4.7,
    totalReviews: 156,
    languages: ["Spanish", "English", "Portuguese"],
    timezone: "America/Mexico_City",
    profileImage: "/placeholder-user.jpg",
    isOnline: true
  }
];

const mockBookings = [
  {
    id: "1",
    teacherName: "Sarah Johnson",
    subject: "Business English",
    scheduledAt: new Date("2024-01-15T10:00:00"),
    duration: 60,
    status: "confirmed",
    price: 25
  },
  {
    id: "2",
    teacherName: "David Chen",
    subject: "Mandarin Conversation",
    scheduledAt: new Date("2024-01-16T14:00:00"),
    duration: 90,
    status: "pending",
    price: 45
  }
];

export default function ClientDashboard() {
  const { user, isLoading, logout } = useAuth();
  const router = useRouter();
  const [teachers, setTeachers] = useState(mockTeachers);
  const [bookings, setBookings] = useState(mockBookings);
  const [searchQuery, setSearchQuery] = useState('');
  const [subjectFilter, setSubjectFilter] = useState('all');
  const [priceFilter, setPriceFilter] = useState('all');
  const [ratingFilter, setRatingFilter] = useState('all');

  useEffect(() => {
    if (isLoading) return;
    if (!user || user.role !== 'client') {
      router.push('/login');
    }
  }, [user, isLoading, router]);

  const handleLogout = () => {
    logout();
    router.push('/');
  };

  const filteredTeachers = teachers.filter(teacher => {
    const matchesSearch = teacher.name.toLowerCase().includes(searchQuery.toLowerCase()) ||
                         teacher.subjects.some(subject => subject.toLowerCase().includes(searchQuery.toLowerCase()));
    
    const matchesSubject = subjectFilter === 'all' || 
                          teacher.subjects.some(subject => subject.toLowerCase().includes(subjectFilter.toLowerCase()));
    
    const matchesPrice = priceFilter === 'all' ||
                        (priceFilter === 'low' && teacher.hourlyRate <= 20) ||
                        (priceFilter === 'medium' && teacher.hourlyRate > 20 && teacher.hourlyRate <= 30) ||
                        (priceFilter === 'high' && teacher.hourlyRate > 30);
    
    const matchesRating = ratingFilter === 'all' ||
                         (ratingFilter === '4+' && teacher.rating >= 4) ||
                         (ratingFilter === '4.5+' && teacher.rating >= 4.5);

    return matchesSearch && matchesSubject && matchesPrice && matchesRating;
  });

  const handleBookLesson = (teacherId: string) => {
    router.push(`/book/${teacherId}`);
  };

  if (isLoading) {
    return (
      <div className="min-h-screen flex items-center justify-center bg-gray-50">
        <p className="text-sm text-gray-600">Loading your dashboard...</p>
      </div>
    );
  }

  if (!user || user.role !== 'client') {
    return null;
  }

  return (
    <div className="min-h-screen bg-gray-50">
      {/* Header */}
      <header className="bg-white shadow-sm border-b">
        <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
          <div className="flex justify-between items-center py-4">
            <div className="flex items-center space-x-4">
              <Avatar className="h-10 w-10">
                <AvatarImage src="/placeholder-user.jpg" />
                <AvatarFallback>{user.name.charAt(0)}</AvatarFallback>
              </Avatar>
              <div>
                <h1 className="text-xl font-semibold">Welcome back, {user.name}</h1>
                <p className="text-sm text-gray-600">Student Dashboard</p>
              </div>
            </div>
            <div className="flex items-center space-x-2">
              <Button variant="outline" size="sm">
                <Settings className="h-4 w-4 mr-2" />
                Settings
              </Button>
              <Button variant="outline" size="sm" onClick={handleLogout}>
                <LogOut className="h-4 w-4 mr-2" />
                Logout
              </Button>
            </div>
          </div>
        </div>
      </header>

      <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-8">
        <Tabs defaultValue="find-teachers" className="space-y-6">
          <TabsList>
            <TabsTrigger value="find-teachers">Find Assistants</TabsTrigger>
            <TabsTrigger value="my-bookings">My Bookings</TabsTrigger>
            <TabsTrigger value="favorites">Favorites</TabsTrigger>
            <TabsTrigger value="profile">Profile</TabsTrigger>
          </TabsList>

          <TabsContent value="find-teachers" className="space-y-6">
            {/* Search and Filters */}
            <Card>
              <CardHeader>
                <CardTitle>Find Your Perfect Assistant</CardTitle>
                <CardDescription>
                  Search and filter assistants based on your learning needs
                </CardDescription>
              </CardHeader>
              <CardContent>
                <div className="space-y-4">
                  {/* Search Bar */}
                  <div className="relative">
                    <Search className="absolute left-3 top-3 h-4 w-4 text-gray-400" />
                    <Input
                      placeholder="Search by assistant name or subject..."
                      value={searchQuery}
                      onChange={(e) => setSearchQuery(e.target.value)}
                      className="pl-10"
                    />
                  </div>
                  
                  {/* Filters */}
                  <div className="grid grid-cols-1 md:grid-cols-3 gap-4">
                    <Select value={subjectFilter} onValueChange={setSubjectFilter}>
                      <SelectTrigger>
                        <SelectValue placeholder="Subject" />
                      </SelectTrigger>
                      <SelectContent>
                        <SelectItem value="all">All Subjects</SelectItem>
                        <SelectItem value="english">English</SelectItem>
                        <SelectItem value="spanish">Spanish</SelectItem>
                        <SelectItem value="mandarin">Mandarin</SelectItem>
                        <SelectItem value="french">French</SelectItem>
                      </SelectContent>
                    </Select>
                    
                    <Select value={priceFilter} onValueChange={setPriceFilter}>
                      <SelectTrigger>
                        <SelectValue placeholder="Price Range" />
                      </SelectTrigger>
                      <SelectContent>
                        <SelectItem value="all">All Prices</SelectItem>
                        <SelectItem value="low">$0 - $20</SelectItem>
                        <SelectItem value="medium">$21 - $30</SelectItem>
                        <SelectItem value="high">$31+</SelectItem>
                      </SelectContent>
                    </Select>
                    
                    <Select value={ratingFilter} onValueChange={setRatingFilter}>
                      <SelectTrigger>
                        <SelectValue placeholder="Rating" />
                      </SelectTrigger>
                      <SelectContent>
                        <SelectItem value="all">All Ratings</SelectItem>
                        <SelectItem value="4+">4.0+ Stars</SelectItem>
                        <SelectItem value="4.5+">4.5+ Stars</SelectItem>
                      </SelectContent>
                    </Select>
                  </div>
                </div>
              </CardContent>
            </Card>

            {/* Assistants List */}
            <div className="grid grid-cols-1 lg:grid-cols-2 gap-6">
              {filteredTeachers.map((teacher) => (
                <Card key={teacher.id} className="hover:shadow-lg transition-shadow">
                  <CardContent className="p-6">
                    <div className="flex items-start space-x-4">
                      <div className="relative">
                        <Avatar className="h-16 w-16">
                          <AvatarImage src={teacher.profileImage} />
                          <AvatarFallback>{teacher.name.charAt(0)}</AvatarFallback>
                        </Avatar>
                        {teacher.isOnline && (
                          <div className="absolute -bottom-1 -right-1 h-4 w-4 bg-green-500 rounded-full border-2 border-white"></div>
                        )}
                      </div>
                      
                      <div className="flex-1 min-w-0">
                        <div className="flex items-center justify-between mb-2">
                          <h3 className="text-lg font-semibold">{teacher.name}</h3>
                          <Button variant="ghost" size="sm">
                            <Heart className="h-4 w-4" />
                          </Button>
                        </div>
                        
                        <div className="flex items-center space-x-4 mb-2">
                          <div className="flex items-center">
                            <Star className="h-4 w-4 text-yellow-400 fill-current mr-1" />
                            <span className="text-sm font-medium">{teacher.rating}</span>
                            <span className="text-sm text-gray-500 ml-1">({teacher.totalReviews})</span>
                          </div>
                          <div className="flex items-center">
                            <DollarSign className="h-4 w-4 text-green-600 mr-1" />
                            <span className="text-sm font-medium">${teacher.hourlyRate}/hr</span>
                          </div>
                        </div>
                        
                        <p className="text-sm text-gray-600 mb-3 line-clamp-2">{teacher.bio}</p>
                        
                        <div className="flex flex-wrap gap-1 mb-3">
                          {teacher.subjects.slice(0, 3).map((subject) => (
                            <Badge key={subject} variant="secondary" className="text-xs">
                              {subject}
                            </Badge>
                          ))}
                        </div>
                        
                        <div className="flex items-center justify-between">
                          <div className="flex items-center space-x-2">
                            <span className="text-xs text-gray-500">Languages:</span>
                            <div className="flex space-x-1">
                              {teacher.languages.map((lang) => (
                                <Badge key={lang} variant="outline" className="text-xs">
                                  {lang}
                                </Badge>
                              ))}
                            </div>
                          </div>
                          <div className="flex space-x-2">
                            <Button variant="outline" size="sm">
                              <MessageCircle className="h-4 w-4 mr-1" />
                              Message
                            </Button>
                            <Button size="sm" onClick={() => handleBookLesson(teacher.id)}>
                              <Calendar className="h-4 w-4 mr-1" />
                              Request Consultation
                            </Button>
                          </div>
                        </div>
                      </div>
                    </div>
                  </CardContent>
                </Card>
              ))}
            </div>
          </TabsContent>

          <TabsContent value="my-bookings" className="space-y-6">
            <Card>
              <CardHeader>
                <CardTitle>My Bookings</CardTitle>
                <CardDescription>
                  View and manage your upcoming and past lessons
                </CardDescription>
              </CardHeader>
              <CardContent>
                <div className="space-y-4">
                  {bookings.map((booking) => (
                    <div key={booking.id} className="flex items-center justify-between p-4 border rounded-lg">
                      <div className="flex items-center space-x-4">
                        <Avatar className="h-12 w-12">
                          <AvatarImage src="/placeholder-user.jpg" />
                          <AvatarFallback>{booking.teacherName.charAt(0)}</AvatarFallback>
                        </Avatar>
                        <div>
                          <p className="font-medium">{booking.teacherName}</p>
                          <p className="text-sm text-gray-600">{booking.subject}</p>
                          <p className="text-sm text-gray-500">
                            {booking.scheduledAt.toLocaleDateString()} at {booking.scheduledAt.toLocaleTimeString()}
                          </p>
                        </div>
                      </div>
                      <div className="text-right">
                        <p className="font-medium">${booking.price}</p>
                        <Badge 
                          variant={booking.status === 'confirmed' ? 'default' : 'secondary'}
                          className="mt-1"
                        >
                          {booking.status}
                        </Badge>
                        <p className="text-sm text-gray-500 mt-1">{booking.duration} min</p>
                      </div>
                    </div>
                  ))}
                </div>
              </CardContent>
            </Card>
          </TabsContent>

          <TabsContent value="favorites">
            <Card>
              <CardHeader>
                <CardTitle>Favorite Assistants</CardTitle>
                <CardDescription>
                  Assistants you've saved for quick access
                </CardDescription>
              </CardHeader>
              <CardContent>
                <div className="text-center py-12">
                  <Heart className="h-12 w-12 text-gray-400 mx-auto mb-4" />
                  <p className="text-gray-600">No favorite assistants yet</p>
                  <p className="text-sm text-gray-500">Heart assistants you like to save them here</p>
                </div>
              </CardContent>
            </Card>
          </TabsContent>

          <TabsContent value="profile">
            <Card>
              <CardHeader>
                <CardTitle>My Profile</CardTitle>
                <CardDescription>
                  Manage your account information and learning preferences
                </CardDescription>
              </CardHeader>
              <CardContent>
                <div className="space-y-6">
                  <div className="flex items-center space-x-4">
                    <Avatar className="h-20 w-20">
                      <AvatarImage src="/placeholder-user.jpg" />
                      <AvatarFallback className="text-lg">{user.name.charAt(0)}</AvatarFallback>
                    </Avatar>
                    <div>
                      <h3 className="text-lg font-medium">{user.name}</h3>
                      <p className="text-gray-600">{user.email}</p>
                      <Button variant="outline" size="sm" className="mt-2">
                        Edit Profile
                      </Button>
                    </div>
                  </div>
                  
                  <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
                    <div>
                      <h4 className="font-medium mb-2">Learning Goals</h4>
                      <p className="text-sm text-gray-600">Set your learning objectives and track progress</p>
                      <Button variant="outline" size="sm" className="mt-2">
                        Set Goals
                      </Button>
                    </div>
                    <div>
                      <h4 className="font-medium mb-2">Timezone</h4>
                      <p className="text-sm text-gray-600">UTC (Coordinated Universal Time)</p>
                      <Button variant="outline" size="sm" className="mt-2">
                        Change Timezone
                      </Button>
                    </div>
                  </div>
                </div>
              </CardContent>
            </Card>
          </TabsContent>
        </Tabs>
      </div>
    </div>
  );
}